import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

import '../../Persik.css';

const Pin1 = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>1ПИН</PanelHeader>

        <Group header={<Header mode="secondary">Выбери</Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pn">
                    Понедельник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="bt">
                    Вторник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="sr">
                    Среда
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="ht">
                    Четверг
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pt">
                    Пятница
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="home">
                    Суббота(Нету)
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="home">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default Pin1;
