import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

const KursZa = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Выбери курс(ЗаОчная)</PanelHeader>

        <Group header={<Header mode="secondary">Курс</Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    1
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    2
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    3
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    4
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    5
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default KursZa;