import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Ht = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Четверг(1ПИН)</PanelHeader>


        <Group>
            <Div>
                <Text weight="regular" style={{ marginBottom: 16 }}>
                    1 пара 8:30 - 10:05 – Основы объектно-ориентированного программирования (л/пр) доц. Гнатюк А.Б. (401)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    2 пара 10:15 – 11:50 – Дискретная математик (л) доц. Туртин Д.В. (307)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    3 пара 12:20 – 13:55 – Тайм-менеджмент (л) ст.пр. Ктреева М.М. (307)/ 1н
                         Право (пр) ст.пр. Пантелеев О.В. (307)/ 2н
                </Text>
            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Ht;