import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Pt = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Пятница(1ПИН)</PanelHeader>


        <Group>
            <Div>
                <Text weight="regular" style={{ marginBottom: 16 }}>
                    1 пара 8:30 - 10:05 – Английский язык доц. Емельянов А.А. (307)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    2 пара 10:15 – 11:50 – Право (пр) ст.пр. Пантелеев О.В. (307)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    3 пара 12:20 – 13:55 – Тайм-менеджмент (пр) ст.пр. Ктреева М.М. (307)/ 1н
                    Этика деловых отношений (пр) доц. Николаева О.А. (307)/ 2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    4 пара 14:25 - 16:00 – Дискретная математика (пр) доц. Тутрин Д.В. (307)/ 1н​
                    Тайм-менеджмент (пр) ст.пр. Ктреева М.М. (307)/ 2н​
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Pt;