import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

import '../../Persik.css';
import KursO from "../../Kurs/KursO";

const GroupO = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Группа</PanelHeader>

        <Group header={<Header mode="secondary">Выбери группу</Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    1Пин
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    1М
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    1Э
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="">
                    1ЭБ
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="home">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default GroupO;
