import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Pn = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Понедельник(1ПИН)</PanelHeader>


        <Group>
            <Div>
                <Text weight="regular" style={{ marginBottom: 16 }}>
                    1 пара 8:30 - 10:05 – Дискретная Математика (л)доц. Туртин Д.В.(307).2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    2 пара 10:15 – 11:50 – Дискретная Математика (л)доц. Туртин Д.В.(307)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    3 пара 12:20 – 13:55 – Дискретная Математика (л)доц. Туртин Д.В.(307).1н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    Элективные дисциплины по физической культуре и спорту (пр) Калмыков К.Б. (спорт зал)/2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    4 пара 14:25 - 16:00 – Этика деловых отношений (пр) доц. Николаева О.А. (307)
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Pn;