import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Bt = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Вторник(1ПИН)</PanelHeader>


        <Group>
            <Div>

                <Text weight="medium" style={{ marginBottom: 16 }}>
                    2 пара 10:15 – 11:50 – Элективные дисциплины по физической культуре (пр) Калмыков К.Б. (спорт зал)
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    3 пара 12:20 – 13:55 – Этика деловых отношений (л) доц. Николаева О. А. (307)/ 1н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    4 пара 14:25 - 16:00 – Этика деловых отношений (пр/л) доц. Николаева О.А. (307)
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Bt;